import { useEffect, useState } from 'react'
import { NavLink, useParams } from 'react-router-dom'
import { useProductContext } from './Context/ProductContext';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';

import './css/AddToCart.css';
import { Container } from 'react-bootstrap-v5';
import BreadcrumbFixedTop from './BreadcrumbFixedTop';
const API = "http://localhost:3001/cardata";

const SingleProduct = () => {
  const { getSingleProduct, isSingleLoading, singleProduct } = useProductContext();
  const [quantity, setQuantity] = useState(1);
  const { id } = useParams();
  useEffect(() => {
    getSingleProduct(`${API}?id=${id}`);
  }, []);
  console.log("Single Product State:", singleProduct);

  if (isSingleLoading) {
    return <div>Loading....</div>
  }


  const handeleDecrement = () => {
    if (quantity > 1) {
      setQuantity(prevCount => prevCount - 1);
    }
  }

  const handeleIncrement = () => {
    if (quantity < 10) {
      setQuantity(prevCount => prevCount + 1);
    }
  }

  return (

    <>
    <BreadcrumbFixedTop Title={singleProduct[0]?.ProductName}/>
      <Container fluid className='p-0'>
        <section className="add-to-bag-main-page">
          <div className="container">
            <div className="row">
              <div className="col-lg-6">
                <div>
                  <img src={singleProduct[0]?.image} alt="nbnk" />
                </div>
              </div>
              <div className="col-lg-6 h-auto add-to-bag-body-content">
                <h4 className="fw-bold mb-1 h4color"><span className="text-success"># </span>{singleProduct[0]?.ProductName}<span
                  className="text-success">#</span></h4>
                <h6 className="title-top-para">{singleProduct[0]?.ProductInfo1}</h6>
                <span className='hrtag' />
                <div className='QuantitySection'>
                  <h5><span>&#8377;</span>{singleProduct[0]?.ProductPrice}</h5>
                  <h6 className='mb-3'>Add Quantity</h6>
                  <div className="d-flex mb-3">
                    <div className="">
                      <button className="btn btn-outline-primary" onClick={handeleDecrement} type="button">-</button>
                    </div>
                    <div className="ms-2">
                      <button className="btn btn-outline-primary" type="text">{quantity}</button>
                    </div>

                    <div className="ms-2">
                      <button className="btn btn-outline-primary" onClick={handeleIncrement} type="button" >+</button>
                    </div>
                  </div>
                </div>
                <div className="mb-3">

                  <h6>More Colors..</h6>
                  <div className="choose-color">
                    <ul className="d-flex p-0 ">
                      <li>
                        <a href="javascript:void(0)" className="btn bg-success"></a>
                      </li>
                      <li>
                        <a href="javascript:void(0)" className="btn bg-primary"></a>
                      </li>
                      <li>
                        <a href="javascript:void(0)" className="btn bg-warning"></a>
                      </li>
                      <li>
                        <a href="javascript:void(0)" className="btn bg-danger"></a>
                      </li>
                      <li>
                        <a href="javascript:void(0)" className="btn bg-info"></a>
                      </li>
                    </ul>
                  </div>
                </div>
                <div>
                  <h6 className="mb-3">Select Size</h6>
                  <div className="add-to-carts-featured-products-sizes-btn mb-4">
                    <ul className="d-flex p-0">
                      <li><a className="btn" href="javascript:void(0)">XS</a></li>
                      <li><a className="btn" href="javascript:void(0)">S</a></li>
                      <li><a className="btn" href="javascript:void(0)">M</a></li>
                      <li><a className="btn" href="javascript:void(0)">L</a></li>
                      <li><a className="btn" href="javascript:void(0)">XL</a></li>
                      <li><a className="btn" href="javascript:void(0)">XXL</a></li>
                    </ul>
                  </div>
                </div>

                <div className="Product-Category d-flex">
                  <h6 className="me-2 h6color">Category:</h6>
                  <p>
                    {singleProduct[0]?.category}
                  </p>
                </div>

                <div className="Product-Brand d-flex">
                  <h6 className="me-2 h6color">Brands:</h6>
                  <p className=''>
                    {singleProduct[0]?.Brands}
                  </p>
                </div>

                <div className="Product-Brand d-flex">
                  <h6 className="me-2 h6color">Availability:</h6>
                  <p>
                    {singleProduct[0]?.Availability}
                  </p>
                </div>
                <div className="mb-3">
                  <div className="add-to-carts-featured-products-add-tobag-whishlist-btn">
                    <div className="mb-2">
                      <NavLink className="btn btn-1 w-75" to="/cart">
                        <AddShoppingCartIcon />
                        Add To Cart
                      </NavLink>
                    </div>
                  </div>
                </div>
                <div className="Product-Category d-flex">
                  <h6 className="me-2">Estimated Delivery:</h6>
                  <p className='text-success fw-medium'>
                    17 July - 23 July
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section >
      </Container>
    </>
  )
};

export default SingleProduct;
