import React from 'react'
import { Link } from 'react-router-dom';

const MainSmallCardItems = (curElem) => {

    const { id, LeftCardImg, ProductName, category , ProductPrice} = curElem;

    return (
        <>
           <Link to={`/singleproduct/${id}`}>
           <div className="card border-0 pb-3 px-1 mb-3">
                <div className="row g-0 align-items-center">
                    <div className="col-3 text-center">
                        <a target="_blank" href="/">
                            <img src={LeftCardImg} className="img-fluid " alt="LeftCardImg" />
                        </a>
                    </div>
                    <div className="col-8">
                        <div className="card-body p-0 px-2">
                            <h5 className="mb-1"> {category}</h5>
                            <h6 className="font-400 font-10">{ProductName}</h6>
                            <div className="d-flex justify-content-between align-items-center flex-wrap rating-wrap">
                                <div className="rating">
                                    <h6 className="home-leftside-wrapper-price"><span>&#8377;</span>{ProductPrice}</h6>
                                </div>
                                <div>
                                    <a href="/" className="home-leftside-wrapper-btn">Add to Cart</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
           </Link>
        </>
    )
}

export default MainSmallCardItems
